import styled from 'styled-components';

export default styled.div`
  display: flex;
  flex-direction: column;
  flex: 1 1 100%;
  width: 450px;
  max-width: 100%;
  height: 100%;
  background: white;
  box-shadow: 0 2px 8px 0 rgba(0,0,0,0.3);
  position: relative;
`;



